<template>
    <section class="registration-cabinet">
        <div class="container">
            <div class="row">
                <div class="col-md-12">
                    <div class="registration-cab-wrap">
                        <h1 class="h1"> </h1>
                        <!-- <label class="form-cab-label"> </label> -->
                        <input
                            type="text"
                            name=""
                            id=""
                            placeholder=""
                            autocomplete="on"
                            class="registration-cab-input"
                            v-model="firstname"
                        />
                        <input
                            type="text"
                            name=""
                            id=""
                            placeholder=""
                            autocomplete="on"
                            class="registration-cab-input"
                            v-model="lastname"
                        />
                        <input
                            type="tel"
                            name=""
                            id=""
                            placeholder=" "
                            autocomplete="on"
                            class="registration-cab-input"
                            v-model="phone"
                        />
                        <input
                            type="password"
                            name=""
                            id=""
                            placeholder=""
                            autocomplete="on"
                            class="registration-cab-input"
                            v-model="password"
                        />
                        <input
                            type="password"
                            name=""
                            id=""
                            placeholder=" "
                            autocomplete="on"
                            class="registration-cab-input"
                            v-model="confirmpassword"
                        />
                        <div class="d-flex confirm">
                            <input type="checkbox" id="checkbox" v-model="checked">
                            <p>  ,       <a href=""> </a></p>
                        </div>
                        <div class="d-flex">
                            <button type="submit" class="registration-cab-button" @click="registerCustomer" >
                                
                            </button>
                            <router-link  to="/personal-cabinet" class="registration-cab-button-create" tag="button">
                                  
                            </router-link>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
import { appLoader } from '@shared/utils/app-loader';
import { mapActions, mapState } from 'vuex';

export default {
  name: 'Registration',
  data() {
    return {
      firstname: '',
      lastname: '',
      phone: '',
      password: '',
      confirmpassword: '',
    };
  },
  computed: {
    ...mapState(['customerId']),
  },
  methods: {
    ...mapActions(['AUTH_CUSTOMER']),
    registerCustomer() {
      appLoader.show();
      this.REGISTER_CUSTOMER({
        LastName: this.lastname,
        Name: this.firstname,
        SecondName: '',
        ClientIIN: this.customerId,
        PhoneNumber: this.phone,
        EMail: '',
        PSW: this.password,
      })
        .then(() => appLoader.hide())
        .catch((err) => {
          appLoader.hide();
          this.$toasted.show(err.message);
        });
    },
  },
};
</script>
